.city {
    display: flex;
    flex-direction: column;
    margin: 20px;
    text-transform: uppercase;

}

.city_label {
    display: none;
    margin-bottom: 5px;
    font-size: 18px;
    color: gray;
}

.city_input {
    width: 220px;
    padding: 10px 0;
    background-color: #fff;
    font-size: 20px;
    outline: 0;
    border-width: 0 0 2px;
    border-color: #ddd
}

.city_input:focus {
    border-color: black
}

@media (min-width: 411px) {
    .city_label {
        display: block;
    }
}

@media (min-width: 768px) {

    .city {
        margin: 20px;
    }

    .city_input {
        width: 300px;
    }
}