.footer {
    width: 220px;
    text-transform: uppercase;
    letter-spacing: 2px;
    text-align: center;
    color: gray;
    font-size: 1.8em;

}

.footer_text::before {
    content: "";
    display: block;
    height: 2px;
    background-color: #ddd;
}

@media (min-width: 768px) {
    .footer {
        margin-top: 10px;
        width: 300px;
    }
}

@media (min-width: 360px) and (orientation: landscape) {
    .footer {
        width: 300px;
    }
}